import * as React from "react";
import { connect } from 'react-redux'
import { Typography, Table } from "antd"
import { fetchPostsIfNeeded } from "../../reduxStore/pagesStore/actions"
const Pages = (props) => {
  const columns = [
    {
      title: 'Name',
      dataIndex: 'name',
      key: 'name',
    },
    {
      title: 'Title',
      dataIndex: 'title',
      key: 'title',
    },
    {
      title: 'Action',
      key: 'Action',
      render: () => {
        return <a target="_blank" href="/app.html">Open in new tab</a>
      }
    },
  ];
  React.useEffect(() => {
    props.getPageData()
  }, [])
  return (
    <div className="pages">
      <Typography.Title level={4}>
        Your application currently contains following pages:
      </Typography.Title>
      <Table rowKey={"name"} dataSource={props.pages || []} columns={columns} />
    </div>
  );
};
const mapStateToProps = state => {
  return (state.pagesStore)
} 
const mapDispatchToProps = (dispatch) => ({
  getPageData: (params) => dispatch(fetchPostsIfNeeded(params))
})
export default connect(mapStateToProps, mapDispatchToProps)(Pages);